@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}




body {
  background: #293855;
  color: #ffffff;
  font-family: 'Poppins', sans-serif;
  /* background: radial-gradient(circle, rgba(255,255,255,0.9836309523809523) 50%, rgba(99,100,122,1) 100%);; */
}
.input-text{
    height: 8vh;
    width: 19vw;
    border: none;
    border-radius: 0.5vh;
    background-color: lightgray;
}

.input-number{
    width: 2vw;
    background: white;
    font-family: 'Poppins';
    color: black;
    border: none;
    border-radius: 0.5vh;
    padding: 1vh;
    margin: 0.5vw 0.5vw 0.5vw 0.5vw;
}

.form-align {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
}

.boton-agregar {
    background-color:#4ca771;
    font-family: 'Poppins';
    color: #ffffff;
    border-radius: 1vh;
    padding: 0.7em;
    /* margin-top: 1.2em; */
    border: 0px;
}

input:focus {
    outline: none;
}
.cantidades {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 2vh;
}

.cant-espacios {
    margin: 0.5vh;
}

.boton-eliminar {
    background-color: #f2ac20;
    border: none;
    border-radius: 0.7vh;
    padding: 2vh;
    color: white;
}

.boton-modificar {
    background-color: #4265d6;
    border: none;
    border-radius: 0.7vh;
    padding: 1.5vh 2.5vh;
    color: #ffffff;
}

.vertical {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: black;
}

.p-color {
    color: black;
}

.list-horizontal {
    background: white;
    border-radius: 0.7em;
    padding: 1.5em;
    width: 15em;
    height: 10em;
    margin: 1em;
}

.order-list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding-left: 50px;
    padding-right: 50px;
}
